.styleForm {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.styleName {
  text-align: center;
  font-style: italic;
  font-weight: 400;
  font-size: 40px;
  margin: 0;
}

.styleInput {
  margin: 10px auto 0 auto;
  width: 300px;
  height: 40px;
  margin-top: 5px;
  border: 3px solid rgb(80, 114, 251);
  border-radius: 7px;
  box-shadow: 1px 2px 2px 1px;
  &:hover {
    border: 3px solid rgb(27, 153, 27);
  }
  &:focus {
    border: 3px solid rgb(27, 153, 27);
  }
  &:active {
    border: 3px solid rgb(27, 153, 27);
  }
}

.styleNumber {
  text-align: center;
  font-style: italic;
  font-weight: 400;
  font-size: 40px;
  margin: 0;
}

.styleButton {
  font-size: 20px;
  margin: 0 auto;
  margin-top: 10px;
  color: rgb(80, 114, 251);
  border: 3px solid rgb(80, 114, 251);
  border-radius: 8px;
  box-shadow: 1px 2px 2px 1px;
  cursor: pointer;
  width: 105px;
  height: 50px;
  background-color: whitesmoke;
  &:hover {
    border: 3px solid rgb(27, 153, 27);
    background-color: rgb(241, 208, 163);
    color: rgb(27, 153, 27);
  }
  &:focus {
    border: 3px solid rgb(27, 153, 27);
    background-color: rgb(241, 208, 163);
    color: rgb(27, 153, 27);
  }
  &:active {
    border: 3px solid rgb(27, 153, 27);
    background-color: rgb(241, 208, 163);
    color: rgb(27, 153, 27);
  }
}
